<template>
  <div class="login_container">
    <div class="login_box">
      <!--      头像区域-->
      <div class="avatar_box">
        <img src="@/assets/logo.png">
      </div>
      <!--      登录表单区域-->
      <el-form label-width="0px" class="login_form" :model="loginForm" :rules="loginFormRules" ref="loginFormRef">
        <!--        用户名-->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <!--        密码-->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" show-password></el-input>
        </el-form-item>
        <!--        按钮区域-->
        <el-form-item class="btns">
          <el-button type="primary" @click="Login">登录</el-button>
          <el-button type="info" @click="resetLoginForm">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      // 登录表单的数据绑定对象
      loginForm: {
        username: '',
        password: ''
      },
      // 表单的验证规则对象
      loginFormRules: {
        username: [
          {
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur'
          }
        ], // 用户名是否合法
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur'
          }
        ] // 密码是否合法
      }
    }
  },
  methods: {
    // 重置登录表单
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    // 表单预验证
    Login: function () {
      this.$refs.loginFormRef.validate(valid => {
        if (!valid) return
        this.$http.post('login', this.loginForm).then(res => {
          if (res.data.token){
            //获取登录用户名
            window.sessionStorage.setItem('username',res.data.data.username)
            //获取登录角色
            window.sessionStorage.setItem('role',res.data.data.role)
            // 登陆成功之后的token, 保存到客户端的 sessionStorage 中
            window.sessionStorage.setItem('token','Bearer' + ' ' + res.data.token)
            if (res.data.data.role === 1){
              this.$message.success('登录成功')
            }else {
              this.$message.warning('只有管理员有登录权限')
            }
            // 通过编程式导航跳转到后台页面， 路由地址是 /home
            this.$router.push('/home')
          }else {
            this.$message.error('登录失败')
          }
        }).catch(err => {
          console.log(err)
          this.$message.error('登录失败')
        })
      })
    }
  }
}
</script>

